import { Component, Prop, Event, EventEmitter, h, Fragment } from '@stencil/core';

export interface BreadcrumbItem {
  label: string;
  path?: string;
  isActive?: boolean;
}

@Component({
  tag: 'bh-breadcrumbs',
  styleUrl: 'bh-breadcrumbs.css',
  shadow: true,
})
export class BhBreadcrumbs {
  /**
   * Array of breadcrumb items
   */
  @Prop() items: BreadcrumbItem[] | string = [];

  /**
   * Separator icon name (Material Symbols)
   */
  @Prop() separator: string = 'chevron_right';

  /**
   * Whether to show the home button
   */
  @Prop() showHome: boolean = true;

  /**
   * Maximum number of visible items before collapsing
   */
  @Prop() maxVisibleItems: number = 4;

  /**
   * Event emitted when a breadcrumb item is clicked
   */
  @Event() bhItemClick!: EventEmitter<BreadcrumbItem>;

  /**
   * Event emitted when navigating to a path
   */
  @Event() bhNavigate!: EventEmitter<string>;

  private get parsedItems(): BreadcrumbItem[] {
    if (typeof this.items === 'string') {
      try {
        return JSON.parse(this.items);
      } catch {
        return [];
      }
    }
    return this.items || [];
  }

  private get displayData() {
    const items = this.parsedItems;
    if (!items || items.length <= this.maxVisibleItems) {
      return {
        displayItems: items ? [...items] : [],
        showEllipsis: false,
        collapsedItems: [],
      };
    } else {
      const lastItems = items.slice(-(this.maxVisibleItems - 2));
      return {
        displayItems: [items[0], ...lastItems],
        showEllipsis: true,
        collapsedItems: items.slice(1, -(this.maxVisibleItems - 2)),
      };
    }
  }

  private handleItemClick(item: BreadcrumbItem) {
    if (!item.isActive && item.path) {
      this.bhItemClick.emit(item);
      this.bhNavigate.emit(item.path);
    }
  }

  private handleHomeClick = () => {
    const homeItem: BreadcrumbItem = {
      label: 'Home',
      path: '/',
    };
    this.bhItemClick.emit(homeItem);
    this.bhNavigate.emit('/');
  };

  private handleEllipsisClick = () => {
    const { collapsedItems } = this.displayData;
    if (collapsedItems.length > 0) {
      this.handleItemClick(collapsedItems[0]);
    }
  };

  private renderBreadcrumbItem(item: BreadcrumbItem, index: number) {
    if (item.isActive || !item.path) {
      return (
        <div
          key={index}
          class="breadcrumb-button breadcrumb-current"
          aria-current="page"
        >
          <span class="breadcrumb-text">{item.label}</span>
        </div>
      );
    }

    return (
      <button
        key={index}
        type="button"
        class="breadcrumb-button"
        onClick={() => this.handleItemClick(item)}
      >
        <span class="breadcrumb-text">{item.label}</span>
      </button>
    );
  }

  private renderSeparator() {
    return (
      <div class="breadcrumb-separator">
        <span class="material-symbols-outlined">{this.separator}</span>
      </div>
    );
  }

  render() {
    const { displayItems, showEllipsis, collapsedItems } = this.displayData;

    return (
      <nav class="breadcrumbs" aria-label="Breadcrumb">
        <div class="breadcrumbs-container">
          {/* Home button */}
          {this.showHome && (
            <div class="breadcrumb-item">
              <bh-button-icon
                hierarchy="quaternary"
                size="xs"
                iconName="cottage"
                onClick={this.handleHomeClick}
                aria-label="Home"
              />
            </div>
          )}

          {/* Separator after home */}
          {this.showHome && (displayItems.length > 0 || showEllipsis) && this.renderSeparator()}

          {/* With ellipsis */}
          {showEllipsis && displayItems.length > 0 && (
            <Fragment>
              <div class="breadcrumb-item">
                {this.renderBreadcrumbItem(displayItems[0], 0)}
              </div>

              {this.renderSeparator()}

              {/* Ellipsis button */}
              <div class="breadcrumb-item">
                <button
                  type="button"
                  class="breadcrumb-button breadcrumb-ellipsis"
                  onClick={this.handleEllipsisClick}
                  title={`Show ${collapsedItems.length} hidden items`}
                >
                  <span class="breadcrumb-text">...</span>
                </button>
              </div>

              {this.renderSeparator()}

              {/* Remaining items */}
              {displayItems.slice(1).map((item, index) => (
                <Fragment>
                  <div class="breadcrumb-item" key={`item-${index + 1}`}>
                    {this.renderBreadcrumbItem(item, index + 1)}
                  </div>

                  {index < displayItems.slice(1).length - 1 && this.renderSeparator()}
                </Fragment>
              ))}
            </Fragment>
          )}

          {/* Without ellipsis */}
          {!showEllipsis && displayItems.map((item, index) => (
            <Fragment>
              <div class="breadcrumb-item" key={`item-${index}`}>
                {this.renderBreadcrumbItem(item, index)}
              </div>

              {index < displayItems.length - 1 && this.renderSeparator()}
            </Fragment>
          ))}
        </div>
      </nav>
    );
  }
}
